<template>
    <div>
        <van-nav-bar
        title="地址列表"
        left-text="返回"
        left-arrow
        @click-left="onAdd"
        />
        <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @add="inAdd"
            @edit="inAdd"
        />
    </div>
</template>

<script>
    export default {
        data(){
            return {
                chosenAddressId: '1',
                list: [ 
                ],
                
            };
        },
        created(){
            this.getaddress();
        },
        methods:{
            inAdd(address){
                
                let o = {
                    name:'Address'
                };
                if(address){
                    o.params = {
                        aid:address.id
                    }
                }
                this.$router.push(o)
            },
            onAdd() {
                // this.$router.push(e)
                this.$router.go(-1)

            },
            getaddress(){
                // var tokenString = localStorage.getItem('login_Tk');
                let tokenString = localStorage.getItem('login_Tk');
                
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })
                this.axios({
                    method:'GET',
                    url:'/findAddress',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    let data = result.data.result;
                    let list = []
                    for(let i = 0 ; i < data.length ; i ++){
                        let a = {
                            id:data[i].aid,
                            name:data[i].name,
                            tel:data[i].tel,
                            address:data[i].province + data[i].city + data[i].county + data[i].addressDetail
                        }

                        list.push(a)
                        
                    }
                    list[1].isDefault = true
                    
                    this.list = list
                }).catch(err=>{
                    
                })
            }
        }
    }
</script>

<style lang="less" scoped>
/deep/ .van-address-item .van-radio__icon--checked .van-icon{
    background-color:#f4b973 ;
    border-color: #f4b973;
}
/deep/ .van-tag--danger{
    background-color:#f4b973 ;
}
/deep/.van-button--danger{
    background-color:#f4b973 ;
    border-color: #f4b973;
}
</style>